import gsap from 'gsap'

export default {
    name: 'LoadingAjax',
    computed: {
        loadingConfig () {
            return this.$store.state.loadingConfig
        },
        isLoading () {
            return this.$store.getters.isLoading
        },
    },
    created () {
        this.$store.commit('CHANGE_LOADING_TYPE', 'LOADING_TYPE_AJAX')
    },
    mounted () {
        this.$refs.ajax.classList.add('start-animation')
        gsap.fromTo('.o-index-opening__full-poly line', { attr: { y2: 5 } }, { attr: { y2: 595 } })
        gsap.set('.o-index-opening__full-poly__full', { attr: { opacity: 0 }, delay: 0.8 })
        gsap.set('.o-index-opening__full-poly__right', { attr: { opacity: 1 }, delay: 0.8 })
        setTimeout(() => {
            this.$store.dispatch('WAIT_LOADING')
        }, 800)
    },
    methods: {
        loadingDone () {
            this.$store.commit('CHANGE_LOADING_TYPE', 'LOADING_TYPE_DEFAULT')
        },
    },
}
