import { useStore, computed } from '@nuxtjs/composition-api'

export default {
    name: 'LoadingDefault',
    setup (props, context) {
        const store = useStore()

        const loadingConfig = computed(() => store.state.loadingConfig)
        const isLoading = computed(() => store.getters.isLoading)

        const loadingDone = () => {
        }

        return {
            loadingConfig,
            isLoading,
            loadingDone,
        }
    },
}
