export default {
    name: 'DefaultLayout',
    inject: ['viewportInfo'],
    data () {
        return {
            isOpenMenu: false,
        }
    },
    computed: {
        globalStyle () {
            const style = {
                '--vh': '1vh',
            }
            if (process.browser) {
                style['--vh'] = `${window.innerHeight / this.viewportInfo.value.vpHeight}vh`
            }
            return style
        },
        loadingConfig () {
            return this.$store.state.loadingConfig
        },
        isLoading () {
            return this.$store.getters.isLoading
        },
    },
    watch: {
        $route: {
            immediate: true,
            async handler (to, from) {
                await this.$parent.$i18n.waitForPendingLocaleChange()
                if (to.meta.name === 'PageIndex') {
                    return
                }
                this.$store.dispatch('WAIT_LOADING')
            },
        },
    },
    methods: {
        openMenu () {
            this.isOpenMenu = true
            document.querySelector('html').classList.add('noscroll')
        },
        closeMenu () {
            this.isOpenMenu = false
            document.querySelector('html').classList.remove('noscroll')
        },
    },
}
