export default function ({ app, route, store, ...context }) {
    app.router.options.scrollBehavior = (to, from, savedPosition) => {
        if (to.meta.loading) {
            store.dispatch('ADD_LOADING_STACK', new Promise((resolve) => { resolve() }))
        }
        // return savedPosition || { x: 0, y: 0 }
    }

    app.router.afterEach((to, from) => {
        if (process.browser) {
            if (to.name === 'courses_zh') {
                const courseFilter = ['cat-program', 'cat-facet']
                const courseQuery = Object.keys(to.query)
                const compare = courseFilter.filter(el => courseQuery.includes(el))
                if (compare.length > 0) {
                    return null
                }
            }

            if (to.name === 'projects_zh') {
                const courseFilter = ['category']
                const courseQuery = Object.keys(to.query)
                const compare = courseFilter.filter(el => courseQuery.includes(el))
                if (compare.length > 0) {
                    return null
                }
            }

            setTimeout(() => {
                window.scrollTo(0, 0)
            }, 500)
        }
    })
}
